import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import { Table, Button } from 'antd';
import axios from 'axios';

class Novel extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      list: []
    }
  }
  render() {
    const columns = [
      {
      key: 'title',
      dataIndex: 'title',
      title: '小说名称',
      align: 'center'
    }, {
      key: 'page_title',
      dataIndex: 'page_title',
      title: '首章标题',
      align: 'center'
    }, {
      key: 'image_url',
      title: 'banner图',
      align: 'center',
      render(item, record, index) {
        const imgStyle = {
          width: '100px',
          verticalAlign: 'middle'
        };
        return (
          <img src={record.image_url} style={imgStyle} alt=""/>
        )
      }
    }, {
      key: 'action',
      title: '操作',
      align: 'center',
      render(item, record, index) {
        return (
          <div>
            <Button style={{marginRight: '10px'}} size='small' type='primary'>
              <Link to={'/build/novel/modify?record=' + JSON.stringify(record)}>修改</Link>
            </Button>
            <Button size='small'>
              <Link to={'/build/novel/config?id=' + record.id + '&title=' + record.title}>配置</Link>
            </Button>
          </div>
        )
      }
    }];
    const { list } = this.state;
    return <Table rowKey={record => record._id} columns={columns} dataSource={list} bordered />
  }

  componentDidMount() {
    axios.get('http://h.tyuss.com/book/all').then(res => {
      let list = res.data.data;
      this.setState({list})
    })
  }
}

export default Novel;